"use client"

import { useState, useEffect } from 'react'
import { Card, CardContent, CardDescription, CardHeader, CardTitle } from '@/components/ui/card'
import { Button } from '@/components/ui/button'
import { Badge } from '@/components/ui/badge'
import { Tabs, TabsContent, TabsList, TabsTrigger } from '@/components/ui/tabs'
import { Input } from '@/components/ui/input'
import { Label } from '@/components/ui/label'
import { Textarea } from '@/components/ui/textarea'
import { Select, SelectContent, SelectItem, SelectTrigger, SelectValue } from '@/components/ui/select'
import { Table, TableBody, TableCell, TableHead, TableHeader, TableRow } from '@/components/ui/table'
import { Dialog, DialogContent, DialogDescription, DialogHeader, DialogTitle, DialogTrigger } from '@/components/ui/dialog'
import { AlertDialog, AlertDialogAction, AlertDialogCancel, AlertDialogContent, AlertDialogDescription, AlertDialogFooter, AlertDialogHeader, AlertDialogTitle, AlertDialogTrigger } from '@/components/ui/alert-dialog'

// Types
interface Book {
  id: string
  title: string
  author: string
  isbn: string
  category: string
  publisher: string
  year: number
  copies: number
  available: number
  location: string
  description?: string
  addedDate: string
}

interface Member {
  id: string
  name: string
  email: string
  phone: string
  address: string
  memberType: 'student' | 'teacher' | 'staff'
  joinDate: string
  status: 'active' | 'inactive'
}

interface Transaction {
  id: string
  bookId: string
  memberId: string
  type: 'borrow' | 'return'
  date: string
  dueDate?: string
  returnDate?: string
  fine?: number
  status: 'active' | 'returned' | 'overdue'
}

export default function LibraryManagement() {
  const [activeTab, setActiveTab] = useState('dashboard')
  const [books, setBooks] = useState<Book[]>([])
  const [members, setMembers] = useState<Member[]>([])
  const [transactions, setTransactions] = useState<Transaction[]>([])
  const [searchTerm, setSearchTerm] = useState('')

  // Load data from localStorage on component mount
  useEffect(() => {
    const savedBooks = localStorage.getItem('library-books')
    const savedMembers = localStorage.getItem('library-members')
    const savedTransactions = localStorage.getItem('library-transactions')

    if (savedBooks) setBooks(JSON.parse(savedBooks))
    if (savedMembers) setMembers(JSON.parse(savedMembers))
    if (savedTransactions) setTransactions(JSON.parse(savedTransactions))
  }, [])

  // Save data to localStorage whenever state changes
  useEffect(() => {
    localStorage.setItem('library-books', JSON.stringify(books))
  }, [books])

  useEffect(() => {
    localStorage.setItem('library-members', JSON.stringify(members))
  }, [members])

  useEffect(() => {
    localStorage.setItem('library-transactions', JSON.stringify(transactions))
  }, [transactions])

  // Book form state
  const [bookForm, setBookForm] = useState({
    title: '',
    author: '',
    isbn: '',
    category: '',
    publisher: '',
    year: new Date().getFullYear(),
    copies: 1,
    location: '',
    description: ''
  })

  // Member form state
  const [memberForm, setMemberForm] = useState({
    name: '',
    email: '',
    phone: '',
    address: '',
    memberType: 'student' as 'student' | 'teacher' | 'staff'
  })

  // Transaction form state
  const [transactionForm, setTransactionForm] = useState({
    bookId: '',
    memberId: '',
    type: 'borrow' as 'borrow' | 'return'
  })

  // Edit states
  const [editingBook, setEditingBook] = useState<Book | null>(null)
  const [editingMember, setEditingMember] = useState<Member | null>(null)
  const [isEditBookDialogOpen, setIsEditBookDialogOpen] = useState(false)
  const [isEditMemberDialogOpen, setIsEditMemberDialogOpen] = useState(false)

  // Add new book
  const addBook = () => {
    if (!bookForm.title || !bookForm.author || !bookForm.isbn) return

    const newBook: Book = {
      id: Date.now().toString(),
      ...bookForm,
      available: bookForm.copies,
      addedDate: new Date().toISOString()
    }

    setBooks([...books, newBook])
    setBookForm({
      title: '',
      author: '',
      isbn: '',
      category: '',
      publisher: '',
      year: new Date().getFullYear(),
      copies: 1,
      location: '',
      description: ''
    })
  }

  // Edit book functions
  const startEditBook = (book: Book) => {
    setEditingBook(book)
    setBookForm({
      title: book.title,
      author: book.author,
      isbn: book.isbn,
      category: book.category,
      publisher: book.publisher,
      year: book.year,
      copies: book.copies,
      location: book.location,
      description: book.description || ''
    })
    setIsEditBookDialogOpen(true)
  }

  const updateBook = () => {
    if (!editingBook || !bookForm.title || !bookForm.author || !bookForm.isbn) return

    const updatedBooks = books.map(book => {
      if (book.id === editingBook.id) {
        const availableDiff = bookForm.copies - book.copies
        return {
          ...book,
          ...bookForm,
          available: Math.max(0, book.available + availableDiff)
        }
      }
      return book
    })

    setBooks(updatedBooks)
    setEditingBook(null)
    setIsEditBookDialogOpen(false)
    setBookForm({
      title: '',
      author: '',
      isbn: '',
      category: '',
      publisher: '',
      year: new Date().getFullYear(),
      copies: 1,
      location: '',
      description: ''
    })
  }

  // Add new member
  const addMember = () => {
    if (!memberForm.name || !memberForm.email) return

    const newMember: Member = {
      id: Date.now().toString(),
      ...memberForm,
      joinDate: new Date().toISOString(),
      status: 'active'
    }

    setMembers([...members, newMember])
    setMemberForm({
      name: '',
      email: '',
      phone: '',
      address: '',
      memberType: 'student'
    })
  }

  // Edit member functions
  const startEditMember = (member: Member) => {
    setEditingMember(member)
    setMemberForm({
      name: member.name,
      email: member.email,
      phone: member.phone,
      address: member.address,
      memberType: member.memberType
    })
    setIsEditMemberDialogOpen(true)
  }

  const updateMember = () => {
    if (!editingMember || !memberForm.name || !memberForm.email) return

    const updatedMembers = members.map(member => {
      if (member.id === editingMember.id) {
        return {
          ...member,
          ...memberForm
        }
      }
      return member
    })

    setMembers(updatedMembers)
    setEditingMember(null)
    setIsEditMemberDialogOpen(false)
    setMemberForm({
      name: '',
      email: '',
      phone: '',
      address: '',
      memberType: 'student'
    })
  }

  // Process transaction
  const processTransaction = () => {
    if (!transactionForm.bookId || !transactionForm.memberId) return

    const book = books.find(b => b.id === transactionForm.bookId)
    if (!book) return

    if (transactionForm.type === 'borrow' && book.available <= 0) {
      alert('Buku tidak tersedia untuk dipinjam')
      return
    }

    const newTransaction: Transaction = {
      id: Date.now().toString(),
      ...transactionForm,
      date: new Date().toISOString(),
      dueDate: transactionForm.type === 'borrow' ? 
        new Date(Date.now() + 14 * 24 * 60 * 60 * 1000).toISOString() : undefined,
      status: transactionForm.type === 'borrow' ? 'active' : 'returned'
    }

    setTransactions([...transactions, newTransaction])

    // Update book availability
    const updatedBooks = books.map(b => {
      if (b.id === transactionForm.bookId) {
        return {
          ...b,
          available: transactionForm.type === 'borrow' ? 
            b.available - 1 : Math.min(b.available + 1, b.copies)
        }
      }
      return b
    })
    setBooks(updatedBooks)

    setTransactionForm({
      bookId: '',
      memberId: '',
      type: 'borrow'
    })
  }

  // Delete book
  const deleteBook = (id: string) => {
    setBooks(books.filter(book => book.id !== id))
  }

  // Delete member
  const deleteMember = (id: string) => {
    setMembers(members.filter(member => member.id !== id))
  }

  // Filter functions
  const filteredBooks = books.filter(book =>
    book.title.toLowerCase().includes(searchTerm.toLowerCase()) ||
    book.author.toLowerCase().includes(searchTerm.toLowerCase()) ||
    book.isbn.includes(searchTerm)
  )

  const filteredMembers = members.filter(member =>
    member.name.toLowerCase().includes(searchTerm.toLowerCase()) ||
    member.email.toLowerCase().includes(searchTerm.toLowerCase())
  )

  // Statistics
  const totalBooks = books.reduce((sum, book) => sum + book.copies, 0)
  const availableBooks = books.reduce((sum, book) => sum + book.available, 0)
  const borrowedBooks = totalBooks - availableBooks
  const activeMembers = members.filter(m => m.status === 'active').length
  const overdueTransactions = transactions.filter(t => 
    t.status === 'active' && t.dueDate && new Date(t.dueDate) < new Date()
  ).length

  // Print functions
  const printReport = (type: string) => {
    const printWindow = window.open('', '_blank')
    if (!printWindow) return

    let content = ''
    const currentDate = new Date().toLocaleDateString('id-ID')

    switch (type) {
      case 'books':
        content = `
          <html>
            <head>
              <title>Laporan Daftar Buku</title>
              <style>
                body { font-family: Arial, sans-serif; margin: 20px; }
                table { width: 100%; border-collapse: collapse; margin-top: 20px; }
                th, td { border: 1px solid #ddd; padding: 8px; text-align: left; }
                th { background-color: #f2f2f2; }
                .header { text-align: center; margin-bottom: 20px; }
              </style>
            </head>
            <body>
              <div class="header">
                <h2>LAPORAN DAFTAR BUKU</h2>
                <p>Tanggal: ${currentDate}</p>
              </div>
              <table>
                <thead>
                  <tr>
                    <th>No</th>
                    <th>Judul</th>
                    <th>Pengarang</th>
                    <th>ISBN</th>
                    <th>Kategori</th>
                    <th>Jumlah</th>
                    <th>Tersedia</th>
                  </tr>
                </thead>
                <tbody>
                  ${books.map((book, index) => `
                    <tr>
                      <td>${index + 1}</td>
                      <td>${book.title}</td>
                      <td>${book.author}</td>
                      <td>${book.isbn}</td>
                      <td>${book.category}</td>
                      <td>${book.copies}</td>
                      <td>${book.available}</td>
                    </tr>
                  `).join('')}
                </tbody>
              </table>
            </body>
          </html>
        `
        break
      case 'members':
        content = `
          <html>
            <head>
              <title>Laporan Daftar Anggota</title>
              <style>
                body { font-family: Arial, sans-serif; margin: 20px; }
                table { width: 100%; border-collapse: collapse; margin-top: 20px; }
                th, td { border: 1px solid #ddd; padding: 8px; text-align: left; }
                th { background-color: #f2f2f2; }
                .header { text-align: center; margin-bottom: 20px; }
              </style>
            </head>
            <body>
              <div class="header">
                <h2>LAPORAN DAFTAR ANGGOTA</h2>
                <p>Tanggal: ${currentDate}</p>
              </div>
              <table>
                <thead>
                  <tr>
                    <th>No</th>
                    <th>Nama</th>
                    <th>Email</th>
                    <th>Telepon</th>
                    <th>Tipe</th>
                    <th>Status</th>
                  </tr>
                </thead>
                <tbody>
                  ${members.map((member, index) => `
                    <tr>
                      <td>${index + 1}</td>
                      <td>${member.name}</td>
                      <td>${member.email}</td>
                      <td>${member.phone}</td>
                      <td>${member.memberType}</td>
                      <td>${member.status}</td>
                    </tr>
                  `).join('')}
                </tbody>
              </table>
            </body>
          </html>
        `
        break
      case 'transactions':
        content = `
          <html>
            <head>
              <title>Laporan Transaksi</title>
              <style>
                body { font-family: Arial, sans-serif; margin: 20px; }
                table { width: 100%; border-collapse: collapse; margin-top: 20px; }
                th, td { border: 1px solid #ddd; padding: 8px; text-align: left; }
                th { background-color: #f2f2f2; }
                .header { text-align: center; margin-bottom: 20px; }
              </style>
            </head>
            <body>
              <div class="header">
                <h2>LAPORAN TRANSAKSI PEMINJAMAN</h2>
                <p>Tanggal: ${currentDate}</p>
              </div>
              <table>
                <thead>
                  <tr>
                    <th>No</th>
                    <th>Buku</th>
                    <th>Anggota</th>
                    <th>Tipe</th>
                    <th>Tanggal</th>
                    <th>Status</th>
                  </tr>
                </thead>
                <tbody>
                  ${transactions.map((transaction, index) => {
                    const book = books.find(b => b.id === transaction.bookId)
                    const member = members.find(m => m.id === transaction.memberId)
                    return `
                      <tr>
                        <td>${index + 1}</td>
                        <td>${book?.title || 'N/A'}</td>
                        <td>${member?.name || 'N/A'}</td>
                        <td>${transaction.type === 'borrow' ? 'Pinjam' : 'Kembali'}</td>
                        <td>${new Date(transaction.date).toLocaleDateString('id-ID')}</td>
                        <td>${transaction.status}</td>
                      </tr>
                    `
                  }).join('')}
                </tbody>
              </table>
            </body>
          </html>
        `
        break
    }

    printWindow.document.write(content)
    printWindow.document.close()
    printWindow.print()
  }

  const printBookLabel = (book: Book) => {
    const printWindow = window.open('', '_blank')
    if (!printWindow) return

    const content = `
      <html>
        <head>
          <title>Label Buku - ${book.title}</title>
          <style>
            body { font-family: Arial, sans-serif; margin: 20px; }
            .label { 
              border: 2px solid #000; 
              padding: 15px; 
              width: 300px; 
              margin: 10px auto;
              text-align: center;
            }
            .title { font-size: 16px; font-weight: bold; margin-bottom: 10px; }
            .author { font-size: 14px; margin-bottom: 5px; }
            .isbn { font-size: 12px; margin-bottom: 5px; }
            .location { font-size: 12px; font-weight: bold; }
          </style>
        </head>
        <body>
          <div class="label">
            <div class="title">${book.title}</div>
            <div class="author">Oleh: ${book.author}</div>
            <div class="isbn">ISBN: ${book.isbn}</div>
            <div class="location">Lokasi: ${book.location}</div>
          </div>
        </body>
      </html>
    `

    printWindow.document.write(content)
    printWindow.document.close()
    printWindow.print()
  }

  return (
    <div className="min-h-screen bg-gray-50">
      <header className="bg-white shadow-sm border-b">
        <div className="max-w-7xl mx-auto px-4 sm:px-6 lg:px-8">
          <div className="flex justify-between items-center py-6">
            <div>
              <h1 className="text-3xl font-bold text-gray-900">Sistem Manajemen Perpustakaan</h1>
              <p className="text-gray-600">Kelola koleksi buku dan anggota perpustakaan</p>
            </div>
          </div>
        </div>
      </header>

      <main className="max-w-7xl mx-auto px-4 sm:px-6 lg:px-8 py-8">
        <Tabs value={activeTab} onValueChange={setActiveTab} className="space-y-6">
          <TabsList className="grid w-full grid-cols-5">
            <TabsTrigger value="dashboard">Dashboard</TabsTrigger>
            <TabsTrigger value="books">Kelola Buku</TabsTrigger>
            <TabsTrigger value="members">Kelola Anggota</TabsTrigger>
            <TabsTrigger value="transactions">Transaksi</TabsTrigger>
            <TabsTrigger value="reports">Laporan</TabsTrigger>
          </TabsList>

          {/* Dashboard Tab */}
          <TabsContent value="dashboard" className="space-y-6">
            <div className="grid grid-cols-1 md:grid-cols-2 lg:grid-cols-4 gap-6">
              <Card>
                <CardHeader className="flex flex-row items-center justify-between space-y-0 pb-2">
                  <CardTitle className="text-sm font-medium">Total Buku</CardTitle>
                </CardHeader>
                <CardContent>
                  <div className="text-2xl font-bold">{totalBooks}</div>
                  <p className="text-xs text-muted-foreground">
                    {books.length} judul berbeda
                  </p>
                </CardContent>
              </Card>

              <Card>
                <CardHeader className="flex flex-row items-center justify-between space-y-0 pb-2">
                  <CardTitle className="text-sm font-medium">Buku Tersedia</CardTitle>
                </CardHeader>
                <CardContent>
                  <div className="text-2xl font-bold">{availableBooks}</div>
                  <p className="text-xs text-muted-foreground">
                    Siap dipinjam
                  </p>
                </CardContent>
              </Card>

              <Card>
                <CardHeader className="flex flex-row items-center justify-between space-y-0 pb-2">
                  <CardTitle className="text-sm font-medium">Sedang Dipinjam</CardTitle>
                </CardHeader>
                <CardContent>
                  <div className="text-2xl font-bold">{borrowedBooks}</div>
                  <p className="text-xs text-muted-foreground">
                    Buku dalam peminjaman
                  </p>
                </CardContent>
              </Card>

              <Card>
                <CardHeader className="flex flex-row items-center justify-between space-y-0 pb-2">
                  <CardTitle className="text-sm font-medium">Anggota Aktif</CardTitle>
                </CardHeader>
                <CardContent>
                  <div className="text-2xl font-bold">{activeMembers}</div>
                  <p className="text-xs text-muted-foreground">
                    {overdueTransactions} terlambat
                  </p>
                </CardContent>
              </Card>
            </div>

            <div className="grid grid-cols-1 lg:grid-cols-2 gap-6">
              <Card>
                <CardHeader>
                  <CardTitle>Buku Terbaru</CardTitle>
                  <CardDescription>5 buku yang baru ditambahkan</CardDescription>
                </CardHeader>
                <CardContent>
                  <div className="space-y-4">
                    {books.slice(-5).reverse().map((book) => (
                      <div key={book.id} className="flex items-center space-x-4">
                        <div className="flex-1 space-y-1">
                          <p className="text-sm font-medium leading-none">{book.title}</p>
                          <p className="text-sm text-muted-foreground">{book.author}</p>
                        </div>
                        <Badge variant={book.available > 0 ? "default" : "secondary"}>
                          {book.available > 0 ? "Tersedia" : "Habis"}
                        </Badge>
                      </div>
                    ))}
                  </div>
                </CardContent>
              </Card>

              <Card>
                <CardHeader>
                  <CardTitle>Transaksi Terbaru</CardTitle>
                  <CardDescription>5 transaksi terakhir</CardDescription>
                </CardHeader>
                <CardContent>
                  <div className="space-y-4">
                    {transactions.slice(-5).reverse().map((transaction) => {
                      const book = books.find(b => b.id === transaction.bookId)
                      const member = members.find(m => m.id === transaction.memberId)
                      return (
                        <div key={transaction.id} className="flex items-center space-x-4">
                          <div className="flex-1 space-y-1">
                            <p className="text-sm font-medium leading-none">{book?.title}</p>
                            <p className="text-sm text-muted-foreground">{member?.name}</p>
                          </div>
                          <Badge variant={transaction.type === 'borrow' ? "default" : "secondary"}>
                            {transaction.type === 'borrow' ? "Pinjam" : "Kembali"}
                          </Badge>
                        </div>
                      )
                    })}
                  </div>
                </CardContent>
              </Card>
            </div>
          </TabsContent>

          {/* Books Tab */}
          <TabsContent value="books" className="space-y-6">
            <div className="flex justify-between items-center">
              <div>
                <h2 className="text-2xl font-bold">Kelola Buku</h2>
                <p className="text-gray-600">Tambah, edit, dan hapus buku dari koleksi</p>
              </div>
              <Dialog>
                <DialogTrigger asChild>
                  <Button>Tambah Buku Baru</Button>
                </DialogTrigger>
                <DialogContent className="max-w-2xl">
                  <DialogHeader>
                    <DialogTitle>Tambah Buku Baru</DialogTitle>
                    <DialogDescription>
                      Masukkan informasi lengkap buku yang akan ditambahkan
                    </DialogDescription>
                  </DialogHeader>
                  <div className="grid grid-cols-2 gap-4">
                    <div className="space-y-2">
                      <Label htmlFor="title">Judul Buku *</Label>
                      <Input
                        id="title"
                        value={bookForm.title}
                        onChange={(e) => setBookForm({...bookForm, title: e.target.value})}
                        placeholder="Masukkan judul buku"
                      />
                    </div>
                    <div className="space-y-2">
                      <Label htmlFor="author">Pengarang *</Label>
                      <Input
                        id="author"
                        value={bookForm.author}
                        onChange={(e) => setBookForm({...bookForm, author: e.target.value})}
                        placeholder="Nama pengarang"
                      />
                    </div>
                    <div className="space-y-2">
                      <Label htmlFor="isbn">ISBN *</Label>
                      <Input
                        id="isbn"
                        value={bookForm.isbn}
                        onChange={(e) => setBookForm({...bookForm, isbn: e.target.value})}
                        placeholder="978-xxx-xxx-xxx-x"
                      />
                    </div>
                    <div className="space-y-2">
                      <Label htmlFor="category">Kategori</Label>
                      <Select value={bookForm.category} onValueChange={(value) => setBookForm({...bookForm, category: value})}>
                        <SelectTrigger>
                          <SelectValue placeholder="Pilih kategori" />
                        </SelectTrigger>
                        <SelectContent>
                          <SelectItem value="fiksi">Fiksi</SelectItem>
                          <SelectItem value="non-fiksi">Non-Fiksi</SelectItem>
                          <SelectItem value="sains">Sains</SelectItem>
                          <SelectItem value="teknologi">Teknologi</SelectItem>
                          <SelectItem value="sejarah">Sejarah</SelectItem>
                          <SelectItem value="biografi">Biografi</SelectItem>
                          <SelectItem value="referensi">Referensi</SelectItem>
                        </SelectContent>
                      </Select>
                    </div>
                    <div className="space-y-2">
                      <Label htmlFor="publisher">Penerbit</Label>
                      <Input
                        id="publisher"
                        value={bookForm.publisher}
                        onChange={(e) => setBookForm({...bookForm, publisher: e.target.value})}
                        placeholder="Nama penerbit"
                      />
                    </div>
                    <div className="space-y-2">
                      <Label htmlFor="year">Tahun Terbit</Label>
                      <Input
                        id="year"
                        type="number"
                        value={bookForm.year}
                        onChange={(e) => setBookForm({...bookForm, year: parseInt(e.target.value)})}
                        placeholder="2024"
                      />
                    </div>
                    <div className="space-y-2">
                      <Label htmlFor="copies">Jumlah Eksemplar</Label>
                      <Input
                        id="copies"
                        type="number"
                        value={bookForm.copies}
                        onChange={(e) => setBookForm({...bookForm, copies: parseInt(e.target.value)})}
                        placeholder="1"
                        min="1"
                      />
                    </div>
                    <div className="space-y-2">
                      <Label htmlFor="location">Lokasi Rak</Label>
                      <Input
                        id="location"
                        value={bookForm.location}
                        onChange={(e) => setBookForm({...bookForm, location: e.target.value})}
                        placeholder="A1-001"
                      />
                    </div>
                    <div className="col-span-2 space-y-2">
                      <Label htmlFor="description">Deskripsi</Label>
                      <Textarea
                        id="description"
                        value={bookForm.description}
                        onChange={(e) => setBookForm({...bookForm, description: e.target.value})}
                        placeholder="Deskripsi singkat tentang buku"
                        rows={3}
                      />
                    </div>
                  </div>
                  <div className="flex justify-end space-x-2">
                    <Button variant="outline">Batal</Button>
                    <Button onClick={addBook}>Simpan Buku</Button>
                  </div>
                </DialogContent>
              </Dialog>

              {/* Edit Book Dialog */}
              <Dialog open={isEditBookDialogOpen} onOpenChange={setIsEditBookDialogOpen}>
                <DialogContent className="max-w-2xl">
                  <DialogHeader>
                    <DialogTitle>Edit Buku</DialogTitle>
                    <DialogDescription>
                      Ubah informasi buku yang sudah ada
                    </DialogDescription>
                  </DialogHeader>
                  <div className="grid grid-cols-2 gap-4">
                    <div className="space-y-2">
                      <Label htmlFor="editTitle">Judul Buku *</Label>
                      <Input
                        id="editTitle"
                        value={bookForm.title}
                        onChange={(e) => setBookForm({...bookForm, title: e.target.value})}
                        placeholder="Masukkan judul buku"
                      />
                    </div>
                    <div className="space-y-2">
                      <Label htmlFor="editAuthor">Pengarang *</Label>
                      <Input
                        id="editAuthor"
                        value={bookForm.author}
                        onChange={(e) => setBookForm({...bookForm, author: e.target.value})}
                        placeholder="Nama pengarang"
                      />
                    </div>
                    <div className="space-y-2">
                      <Label htmlFor="editIsbn">ISBN *</Label>
                      <Input
                        id="editIsbn"
                        value={bookForm.isbn}
                        onChange={(e) => setBookForm({...bookForm, isbn: e.target.value})}
                        placeholder="978-xxx-xxx-xxx-x"
                      />
                    </div>
                    <div className="space-y-2">
                      <Label htmlFor="editCategory">Kategori</Label>
                      <Select value={bookForm.category} onValueChange={(value) => setBookForm({...bookForm, category: value})}>
                        <SelectTrigger>
                          <SelectValue placeholder="Pilih kategori" />
                        </SelectTrigger>
                        <SelectContent>
                          <SelectItem value="fiksi">Fiksi</SelectItem>
                          <SelectItem value="non-fiksi">Non-Fiksi</SelectItem>
                          <SelectItem value="sains">Sains</SelectItem>
                          <SelectItem value="teknologi">Teknologi</SelectItem>
                          <SelectItem value="sejarah">Sejarah</SelectItem>
                          <SelectItem value="biografi">Biografi</SelectItem>
                          <SelectItem value="referensi">Referensi</SelectItem>
                        </SelectContent>
                      </Select>
                    </div>
                    <div className="space-y-2">
                      <Label htmlFor="editPublisher">Penerbit</Label>
                      <Input
                        id="editPublisher"
                        value={bookForm.publisher}
                        onChange={(e) => setBookForm({...bookForm, publisher: e.target.value})}
                        placeholder="Nama penerbit"
                      />
                    </div>
                    <div className="space-y-2">
                      <Label htmlFor="editYear">Tahun Terbit</Label>
                      <Input
                        id="editYear"
                        type="number"
                        value={bookForm.year}
                        onChange={(e) => setBookForm({...bookForm, year: parseInt(e.target.value)})}
                        placeholder="2024"
                      />
                    </div>
                    <div className="space-y-2">
                      <Label htmlFor="editCopies">Jumlah Eksemplar</Label>
                      <Input
                        id="editCopies"
                        type="number"
                        value={bookForm.copies}
                        onChange={(e) => setBookForm({...bookForm, copies: parseInt(e.target.value)})}
                        placeholder="1"
                        min="1"
                      />
                    </div>
                    <div className="space-y-2">
                      <Label htmlFor="editLocation">Lokasi Rak</Label>
                      <Input
                        id="editLocation"
                        value={bookForm.location}
                        onChange={(e) => setBookForm({...bookForm, location: e.target.value})}
                        placeholder="A1-001"
                      />
                    </div>
                    <div className="col-span-2 space-y-2">
                      <Label htmlFor="editDescription">Deskripsi</Label>
                      <Textarea
                        id="editDescription"
                        value={bookForm.description}
                        onChange={(e) => setBookForm({...bookForm, description: e.target.value})}
                        placeholder="Deskripsi singkat tentang buku"
                        rows={3}
                      />
                    </div>
                  </div>
                  <div className="flex justify-end space-x-2">
                    <Button variant="outline" onClick={() => setIsEditBookDialogOpen(false)}>Batal</Button>
                    <Button onClick={updateBook}>Update Buku</Button>
                  </div>
                </DialogContent>
              </Dialog>
            </div>

            <div className="flex items-center space-x-2">
              <Input
                placeholder="Cari buku berdasarkan judul, pengarang, atau ISBN..."
                value={searchTerm}
                onChange={(e) => setSearchTerm(e.target.value)}
                className="max-w-sm"
              />
            </div>

            <Card>
              <CardContent className="p-0">
                <Table>
                  <TableHeader>
                    <TableRow>
                      <TableHead>Judul</TableHead>
                      <TableHead>Pengarang</TableHead>
                      <TableHead>ISBN</TableHead>
                      <TableHead>Kategori</TableHead>
                      <TableHead>Tersedia/Total</TableHead>
                      <TableHead>Lokasi</TableHead>
                      <TableHead>Aksi</TableHead>
                    </TableRow>
                  </TableHeader>
                  <TableBody>
                    {filteredBooks.map((book) => (
                      <TableRow key={book.id}>
                        <TableCell className="font-medium">{book.title}</TableCell>
                        <TableCell>{book.author}</TableCell>
                        <TableCell>{book.isbn}</TableCell>
                        <TableCell>
                          <Badge variant="outline">{book.category}</Badge>
                        </TableCell>
                        <TableCell>
                          <Badge variant={book.available > 0 ? "default" : "secondary"}>
                            {book.available}/{book.copies}
                          </Badge>
                        </TableCell>
                        <TableCell>{book.location}</TableCell>
                        <TableCell>
                          <div className="flex space-x-2">
                            <Button
                              size="sm"
                              variant="outline"
                              onClick={() => startEditBook(book)}
                            >
                              Edit
                            </Button>
                            <Button
                              size="sm"
                              variant="outline"
                              onClick={() => printBookLabel(book)}
                            >
                              Cetak Label
                            </Button>
                            <AlertDialog>
                              <AlertDialogTrigger asChild>
                                <Button size="sm" variant="destructive">Hapus</Button>
                              </AlertDialogTrigger>
                              <AlertDialogContent>
                                <AlertDialogHeader>
                                  <AlertDialogTitle>Hapus Buku</AlertDialogTitle>
                                  <AlertDialogDescription>
                                    Apakah Anda yakin ingin menghapus buku "{book.title}"? 
                                    Tindakan ini tidak dapat dibatalkan.
                                  </AlertDialogDescription>
                                </AlertDialogHeader>
                                <AlertDialogFooter>
                                  <AlertDialogCancel>Batal</AlertDialogCancel>
                                  <AlertDialogAction onClick={() => deleteBook(book.id)}>
                                    Hapus
                                  </AlertDialogAction>
                                </AlertDialogFooter>
                              </AlertDialogContent>
                            </AlertDialog>
                          </div>
                        </TableCell>
                      </TableRow>
                    ))}
                  </TableBody>
                </Table>
              </CardContent>
            </Card>
          </TabsContent>

          {/* Members Tab */}
          <TabsContent value="members" className="space-y-6">
            <div className="flex justify-between items-center">
              <div>
                <h2 className="text-2xl font-bold">Kelola Anggota</h2>
                <p className="text-gray-600">Tambah dan kelola anggota perpustakaan</p>
              </div>
              <Dialog>
                <DialogTrigger asChild>
                  <Button>Tambah Anggota Baru</Button>
                </DialogTrigger>
                <DialogContent>
                  <DialogHeader>
                    <DialogTitle>Tambah Anggota Baru</DialogTitle>
                    <DialogDescription>
                      Masukkan informasi anggota perpustakaan
                    </DialogDescription>
                  </DialogHeader>
                  <div className="space-y-4">
                    <div className="space-y-2">
                      <Label htmlFor="memberName">Nama Lengkap *</Label>
                      <Input
                        id="memberName"
                        value={memberForm.name}
                        onChange={(e) => setMemberForm({...memberForm, name: e.target.value})}
                        placeholder="Masukkan nama lengkap"
                      />
                    </div>
                    <div className="space-y-2">
                      <Label htmlFor="memberEmail">Email *</Label>
                      <Input
                        id="memberEmail"
                        type="email"
                        value={memberForm.email}
                        onChange={(e) => setMemberForm({...memberForm, email: e.target.value})}
                        placeholder="email@example.com"
                      />
                    </div>
                    <div className="space-y-2">
                      <Label htmlFor="memberPhone">Telepon</Label>
                      <Input
                        id="memberPhone"
                        value={memberForm.phone}
                        onChange={(e) => setMemberForm({...memberForm, phone: e.target.value})}
                        placeholder="08xxxxxxxxxx"
                      />
                    </div>
                    <div className="space-y-2">
                      <Label htmlFor="memberAddress">Alamat</Label>
                      <Textarea
                        id="memberAddress"
                        value={memberForm.address}
                        onChange={(e) => setMemberForm({...memberForm, address: e.target.value})}
                        placeholder="Alamat lengkap"
                        rows={3}
                      />
                    </div>
                    <div className="space-y-2">
                      <Label htmlFor="memberType">Tipe Anggota</Label>
                      <Select value={memberForm.memberType} onValueChange={(value: 'student' | 'teacher' | 'staff') => setMemberForm({...memberForm, memberType: value})}>
                        <SelectTrigger>
                          <SelectValue placeholder="Pilih tipe anggota" />
                        </SelectTrigger>
                        <SelectContent>
                          <SelectItem value="student">Siswa</SelectItem>
                          <SelectItem value="teacher">Guru</SelectItem>
                          <SelectItem value="staff">Staff</SelectItem>
                        </SelectContent>
                      </Select>
                    </div>
                  </div>
                  <div className="flex justify-end space-x-2">
                    <Button variant="outline">Batal</Button>
                    <Button onClick={addMember}>Simpan Anggota</Button>
                  </div>
                </DialogContent>
              </Dialog>

              {/* Edit Member Dialog */}
              <Dialog open={isEditMemberDialogOpen} onOpenChange={setIsEditMemberDialogOpen}>
                <DialogContent>
                  <DialogHeader>
                    <DialogTitle>Edit Anggota</DialogTitle>
                    <DialogDescription>
                      Ubah informasi anggota perpustakaan
                    </DialogDescription>
                  </DialogHeader>
                  <div className="space-y-4">
                    <div className="space-y-2">
                      <Label htmlFor="editMemberName">Nama Lengkap *</Label>
                      <Input
                        id="editMemberName"
                        value={memberForm.name}
                        onChange={(e) => setMemberForm({...memberForm, name: e.target.value})}
                        placeholder="Masukkan nama lengkap"
                      />
                    </div>
                    <div className="space-y-2">
                      <Label htmlFor="editMemberEmail">Email *</Label>
                      <Input
                        id="editMemberEmail"
                        type="email"
                        value={memberForm.email}
                        onChange={(e) => setMemberForm({...memberForm, email: e.target.value})}
                        placeholder="email@example.com"
                      />
                    </div>
                    <div className="space-y-2">
                      <Label htmlFor="editMemberPhone">Telepon</Label>
                      <Input
                        id="editMemberPhone"
                        value={memberForm.phone}
                        onChange={(e) => setMemberForm({...memberForm, phone: e.target.value})}
                        placeholder="08xxxxxxxxxx"
                      />
                    </div>
                    <div className="space-y-2">
                      <Label htmlFor="editMemberAddress">Alamat</Label>
                      <Textarea
                        id="editMemberAddress"
                        value={memberForm.address}
                        onChange={(e) => setMemberForm({...memberForm, address: e.target.value})}
                        placeholder="Alamat lengkap"
                        rows={3}
                      />
                    </div>
                    <div className="space-y-2">
                      <Label htmlFor="editMemberType">Tipe Anggota</Label>
                      <Select value={memberForm.memberType} onValueChange={(value: 'student' | 'teacher' | 'staff') => setMemberForm({...memberForm, memberType: value})}>
                        <SelectTrigger>
                          <SelectValue placeholder="Pilih tipe anggota" />
                        </SelectTrigger>
                        <SelectContent>
                          <SelectItem value="student">Siswa</SelectItem>
                          <SelectItem value="teacher">Guru</SelectItem>
                          <SelectItem value="staff">Staff</SelectItem>
                        </SelectContent>
                      </Select>
                    </div>
                  </div>
                  <div className="flex justify-end space-x-2">
                    <Button variant="outline" onClick={() => setIsEditMemberDialogOpen(false)}>Batal</Button>
                    <Button onClick={updateMember}>Update Anggota</Button>
                  </div>
                </DialogContent>
              </Dialog>
            </div>

            <div className="flex items-center space-x-2">
              <Input
                placeholder="Cari anggota berdasarkan nama atau email..."
                value={searchTerm}
                onChange={(e) => setSearchTerm(e.target.value)}
                className="max-w-sm"
              />
            </div>

            <Card>
              <CardContent className="p-0">
                <Table>
                  <TableHeader>
                    <TableRow>
                      <TableHead>Nama</TableHead>
                      <TableHead>Email</TableHead>
                      <TableHead>Telepon</TableHead>
                      <TableHead>Tipe</TableHead>
                      <TableHead>Status</TableHead>
                      <TableHead>Tanggal Bergabung</TableHead>
                      <TableHead>Aksi</TableHead>
                    </TableRow>
                  </TableHeader>
                  <TableBody>
                    {filteredMembers.map((member) => (
                      <TableRow key={member.id}>
                        <TableCell className="font-medium">{member.name}</TableCell>
                        <TableCell>{member.email}</TableCell>
                        <TableCell>{member.phone}</TableCell>
                        <TableCell>
                          <Badge variant="outline">
                            {member.memberType === 'student' ? 'Siswa' : 
                             member.memberType === 'teacher' ? 'Guru' : 'Staff'}
                          </Badge>
                        </TableCell>
                        <TableCell>
                          <Badge variant={member.status === 'active' ? "default" : "secondary"}>
                            {member.status === 'active' ? 'Aktif' : 'Tidak Aktif'}
                          </Badge>
                        </TableCell>
                        <TableCell>{new Date(member.joinDate).toLocaleDateString('id-ID')}</TableCell>
                        <TableCell>
                          <div className="flex space-x-2">
                            <Button
                              size="sm"
                              variant="outline"
                              onClick={() => startEditMember(member)}
                            >
                              Edit
                            </Button>
                            <AlertDialog>
                              <AlertDialogTrigger asChild>
                                <Button size="sm" variant="destructive">Hapus</Button>
                              </AlertDialogTrigger>
                              <AlertDialogContent>
                                <AlertDialogHeader>
                                  <AlertDialogTitle>Hapus Anggota</AlertDialogTitle>
                                  <AlertDialogDescription>
                                    Apakah Anda yakin ingin menghapus anggota "{member.name}"? 
                                    Tindakan ini tidak dapat dibatalkan.
                                  </AlertDialogDescription>
                                </AlertDialogHeader>
                                <AlertDialogFooter>
                                  <AlertDialogCancel>Batal</AlertDialogCancel>
                                  <AlertDialogAction onClick={() => deleteMember(member.id)}>
                                    Hapus
                                  </AlertDialogAction>
                                </AlertDialogFooter>
                              </AlertDialogContent>
                            </AlertDialog>
                          </div>
                        </TableCell>
                      </TableRow>
                    ))}
                  </TableBody>
                </Table>
              </CardContent>
            </Card>
          </TabsContent>

          {/* Transactions Tab */}
          <TabsContent value="transactions" className="space-y-6">
            <div className="flex justify-between items-center">
              <div>
                <h2 className="text-2xl font-bold">Transaksi Peminjaman</h2>
                <p className="text-gray-600">Kelola peminjaman dan pengembalian buku</p>
              </div>
              <Dialog>
                <DialogTrigger asChild>
                  <Button>Transaksi Baru</Button>
                </DialogTrigger>
                <DialogContent>
                  <DialogHeader>
                    <DialogTitle>Transaksi Baru</DialogTitle>
                    <DialogDescription>
                      Proses peminjaman atau pengembalian buku
                    </DialogDescription>
                  </DialogHeader>
                  <div className="space-y-4">
                    <div className="space-y-2">
                      <Label htmlFor="transactionType">Tipe Transaksi</Label>
                      <Select value={transactionForm.type} onValueChange={(value: 'borrow' | 'return') => setTransactionForm({...transactionForm, type: value})}>
                        <SelectTrigger>
                          <SelectValue placeholder="Pilih tipe transaksi" />
                        </SelectTrigger>
                        <SelectContent>
                          <SelectItem value="borrow">Peminjaman</SelectItem>
                          <SelectItem value="return">Pengembalian</SelectItem>
                        </SelectContent>
                      </Select>
                    </div>
                    <div className="space-y-2">
                      <Label htmlFor="transactionBook">Buku</Label>
                      <Select value={transactionForm.bookId} onValueChange={(value) => setTransactionForm({...transactionForm, bookId: value})}>
                        <SelectTrigger>
                          <SelectValue placeholder="Pilih buku" />
                        </SelectTrigger>
                        <SelectContent>
                          {books.map((book) => (
                            <SelectItem key={book.id} value={book.id}>
                              {book.title} - {book.author} ({book.available} tersedia)
                            </SelectItem>
                          ))}
                        </SelectContent>
                      </Select>
                    </div>
                    <div className="space-y-2">
                      <Label htmlFor="transactionMember">Anggota</Label>
                      <Select value={transactionForm.memberId} onValueChange={(value) => setTransactionForm({...transactionForm, memberId: value})}>
                        <SelectTrigger>
                          <SelectValue placeholder="Pilih anggota" />
                        </SelectTrigger>
                        <SelectContent>
                          {members.filter(m => m.status === 'active').map((member) => (
                            <SelectItem key={member.id} value={member.id}>
                              {member.name} - {member.email}
                            </SelectItem>
                          ))}
                        </SelectContent>
                      </Select>
                    </div>
                  </div>
                  <div className="flex justify-end space-x-2">
                    <Button variant="outline">Batal</Button>
                    <Button onClick={processTransaction}>Proses Transaksi</Button>
                  </div>
                </DialogContent>
              </Dialog>
            </div>

            <Card>
              <CardContent className="p-0">
                <Table>
                  <TableHeader>
                    <TableRow>
                      <TableHead>Tanggal</TableHead>
                      <TableHead>Buku</TableHead>
                      <TableHead>Anggota</TableHead>
                      <TableHead>Tipe</TableHead>
                      <TableHead>Jatuh Tempo</TableHead>
                      <TableHead>Status</TableHead>
                      <TableHead>Aksi</TableHead>
                    </TableRow>
                  </TableHeader>
                  <TableBody>
                    {transactions.slice().reverse().map((transaction) => {
                      const book = books.find(b => b.id === transaction.bookId)
                      const member = members.find(m => m.id === transaction.memberId)
                      const isOverdue = transaction.dueDate && new Date(transaction.dueDate) < new Date() && transaction.status === 'active'
                      
                      return (
                        <TableRow key={transaction.id}>
                          <TableCell>{new Date(transaction.date).toLocaleDateString('id-ID')}</TableCell>
                          <TableCell className="font-medium">{book?.title || 'N/A'}</TableCell>
                          <TableCell>{member?.name || 'N/A'}</TableCell>
                          <TableCell>
                            <Badge variant={transaction.type === 'borrow' ? "default" : "secondary"}>
                              {transaction.type === 'borrow' ? 'Pinjam' : 'Kembali'}
                            </Badge>
                          </TableCell>
                          <TableCell>
                            {transaction.dueDate ? new Date(transaction.dueDate).toLocaleDateString('id-ID') : '-'}
                          </TableCell>
                          <TableCell>
                            <Badge variant={isOverdue ? "destructive" : transaction.status === 'active' ? "default" : "secondary"}>
                              {isOverdue ? 'Terlambat' : 
                               transaction.status === 'active' ? 'Aktif' : 'Selesai'}
                            </Badge>
                          </TableCell>
                        </TableRow>
                      )
                    })}
                  </TableBody>
                </Table>
              </CardContent>
            </Card>
          </TabsContent>

          {/* Reports Tab */}
          <TabsContent value="reports" className="space-y-6">
            <div>
              <h2 className="text-2xl font-bold">Laporan</h2>
              <p className="text-gray-600">Generate dan cetak berbagai laporan perpustakaan</p>
            </div>

            <div className="grid grid-cols-1 md:grid-cols-3 gap-6">
              <Card>
                <CardHeader>
                  <CardTitle>Laporan Daftar Buku</CardTitle>
                  <CardDescription>
                    Daftar lengkap semua buku dalam koleksi perpustakaan
                  </CardDescription>
                </CardHeader>
                <CardContent>
                  <div className="space-y-2">
                    <p className="text-sm text-muted-foreground">
                      Total: {books.length} judul buku
                    </p>
                    <p className="text-sm text-muted-foreground">
                      Eksemplar: {totalBooks} buku
                    </p>
                  </div>
                  <Button 
                    className="w-full mt-4" 
                    onClick={() => printReport('books')}
                  >
                    Cetak Laporan Buku
                  </Button>
                </CardContent>
              </Card>

              <Card>
                <CardHeader>
                  <CardTitle>Laporan Daftar Anggota</CardTitle>
                  <CardDescription>
                    Daftar lengkap semua anggota perpustakaan
                  </CardDescription>
                </CardHeader>
                <CardContent>
                  <div className="space-y-2">
                    <p className="text-sm text-muted-foreground">
                      Total: {members.length} anggota
                    </p>
                    <p className="text-sm text-muted-foreground">
                      Aktif: {activeMembers} anggota
                    </p>
                  </div>
                  <Button 
                    className="w-full mt-4" 
                    onClick={() => printReport('members')}
                  >
                    Cetak Laporan Anggota
                  </Button>
                </CardContent>
              </Card>

              <Card>
                <CardHeader>
                  <CardTitle>Laporan Transaksi</CardTitle>
                  <CardDescription>
                    Riwayat semua transaksi peminjaman dan pengembalian
                  </CardDescription>
                </CardHeader>
                <CardContent>
                  <div className="space-y-2">
                    <p className="text-sm text-muted-foreground">
                      Total: {transactions.length} transaksi
                    </p>
                    <p className="text-sm text-muted-foreground">
                      Aktif: {transactions.filter(t => t.status === 'active').length} peminjaman
                    </p>
                  </div>
                  <Button 
                    className="w-full mt-4" 
                    onClick={() => printReport('transactions')}
                  >
                    Cetak Laporan Transaksi
                  </Button>
                </CardContent>
              </Card>
            </div>

            <Card>
              <CardHeader>
                <CardTitle>Statistik Perpustakaan</CardTitle>
                <CardDescription>Ringkasan statistik keseluruhan</CardDescription>
              </CardHeader>
              <CardContent>
                <div className="grid grid-cols-2 md:grid-cols-4 gap-4">
                  <div className="text-center">
                    <div className="text-2xl font-bold text-blue-600">{books.length}</div>
                    <div className="text-sm text-muted-foreground">Judul Buku</div>
                  </div>
                  <div className="text-center">
                    <div className="text-2xl font-bold text-green-600">{totalBooks}</div>
                    <div className="text-sm text-muted-foreground">Total Eksemplar</div>
                  </div>
                  <div className="text-center">
                    <div className="text-2xl font-bold text-orange-600">{members.length}</div>
                    <div className="text-sm text-muted-foreground">Total Anggota</div>
                  </div>
                  <div className="text-center">
                    <div className="text-2xl font-bold text-purple-600">{transactions.length}</div>
                    <div className="text-sm text-muted-foreground">Total Transaksi</div>
                  </div>
                </div>
              </CardContent>
            </Card>
          </TabsContent>
        </Tabs>
      </main>
    </div>
  )
}
